.headerInner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.brand {
  color: #222;
  font-weight: 600;
  text-decoration: none;
}

.menuButton {
  display: none;
  background: transparent;
  border: 1px solid #ddd;
  padding: 6px 10px;
  border-radius: 4px;
  color: #333;
}

.nav {
  display: flex;
  gap: 16px;
}

.nav a {
  color: #666;
  text-decoration: none;
}

.selected {
  color: #111;
  border-bottom: 2px solid #111;
}

@media (max-width: 768px) {
  .menuButton {
    display: inline-block;
  }
  .nav {
    display: none;
    position: absolute;
    top: 60px;
    right: 16px;
    background: #fff;
    border: 1px solid #eee;
    border-radius: 8px;
    padding: 12px;
    flex-direction: column;
    gap: 8px;
  }
  .open {
    display: flex;
  }
}


